import React from 'react';
import { shell } from '../../services/electronAPI';

class Link extends React.Component {
  shouldComponentUpdate(nextProps) {
    return nextProps.href !== this.props.href ||
    nextProps.className !== this.props.className ||
    nextProps.children !== this.props.children;
  }
  render() {
    const {
      children,
      href,
      newWindow,
      onClick,
      ...rest
    } = this.props;
    const useBlank = newWindow && href;
    let onClickFn = onClick;
    if (href && !onClick) {
      onClickFn = (event) => {
        event.preventDefault();
        shell.openExternal(href);
      };
    }
    return (
      <a
        {...rest}
        href={href}
        target={useBlank && '_blank'}
        rel={useBlank && 'noopener noreferrer'}
        onClick={onClickFn}
      >
        {children}
      </a>
    );
  }
}

Link.propTypes = {
  href: React.PropTypes.string,
  newWindow: React.PropTypes.bool,
  onClick: React.PropTypes.func,
  className: React.PropTypes.string,
  children: React.PropTypes.oneOfType([
    React.PropTypes.any,
  ]).isRequired,
};

export default Link;



