import { connect } from 'react-redux';
import { ipcRenderer } from '../services/electronAPI';
import Header from '../components/Header/Header';
import * as selectors from '../reducers/selectors';
import { agentStatus } from '../constants/enums';
import * as common from './Business/common';
import * as preferenceEnums from '../constants/enums/preference';
import * as uiActions from '../actions/ui';
import * as channelsType from '../constants/enums/channels';
import memoryStore from '../services/memoryStore';
import { dispatchChangedStatus } from '../subscriptions/channelCenter';

const mapStateToProps = (state) => {
  const { isWeb,
        dashBoardUrl,
        purchaseUrl,
        loginUrl,
        autoLoginUrl } = common.getAgentUrls(state);

  const headerPropsKey = 'header_props';
  const agentId = selectors.getCurrentAgentId(state);
  let headerProps = memoryStore.get(headerPropsKey, agentId);
  const statusCode = selectors.getCurrentAgentStatus(state);
  if (headerProps && headerProps.statusCode === statusCode) {
    headerProps.agentStatus = selectors.getAgentStatus(state);
    return headerProps;
  }
  const ligInInfo = selectors.getLigInInfo(state);
  headerProps = {
    siteId: selectors.getSiteId(state),
    agent: ligInInfo.agent,
    token: ligInInfo.token,
    agentStatus: selectors.getAgentStatus(state),
    settings: [
      { desc: 'Chat', type: preferenceEnums.preferenceTabs.chat },
      { desc: 'Language', type: preferenceEnums.preferenceTabs.language },
      {
        desc: selectors.getIfSupportNotification() ? 'Sound & Popup' : 'Sound',
        type: preferenceEnums.preferenceTabs.soundpopup,
      },
      { desc: 'Columns', type: preferenceEnums.preferenceTabs.columns },
      { desc: 'Others', type: preferenceEnums.preferenceTabs.others },
    ],
    agentName: selectors.getCurrentAgentName(state),
    statusCode,
    agentInfo: {
      isWeb,
      dashBoardUrl,
      purchaseUrl,
      loginUrl,
      autoLoginUrl,
      trialRemind: common.getTrialInfo(state),
    },
  };
  memoryStore.set(headerPropsKey, headerProps, agentId);
  return headerProps;
};

const mapDispatchToProps = dispatch => (
  {
    clickSetting(item) {
      dispatch(uiActions.showSettingsModal(item.type));
    },
    clickStatus(item) {
      if (dispatchChangedStatus(item.code)) {
        return;
      }
      if (item.code !== agentStatus.exit &&
          item.code !== agentStatus.logout) {
        ipcRenderer.send(channelsType.changeStatus, item);
      }
    },
    clickPurchase(agentInfo) {
      common.gotoPurchase(agentInfo);
    },
    gotoDashBoard(agentInfo) {
      common.gotoDashboad(agentInfo);
    },
  }
);

export default connect(mapStateToProps, mapDispatchToProps)(Header);



